import { useEffect, useState } from "react";
import {
  Package,
  Truck,
  CheckCircle,
  Clock,
  Search,
  ChevronLeft,
  ChevronRight,
  Calendar,
  Filter,
  AlertTriangle,
  RefreshCw,
} from "lucide-react";

// Function to get cookies by name
const getCookie = (name) => {
  const value = `; ${document.cookie}`;
  const parts = value.split(`; ${name}=`);
  if (parts.length === 2) return parts.pop().split(";").shift();
  return null;
};

// Helper function to format price in Farsi with Toman
const formatPrice = (price) => {
  return new Intl.NumberFormat("fa-IR").format(price) + " تومان";
};

// Helper function to format API date to Jalali/Persian date format
const formatDate = (dateString) => {
  try {
    const date = new Date(dateString);
    // This is a simple conversion. In production, you'd use a proper Jalali calendar library
    const year = 1403 + (date.getFullYear() - 2024); // Approximate conversion
    const month = date.getMonth() + 1;
    const day = date.getDate();
    return `${year}/${month.toString().padStart(2, "0")}/${day
      .toString()
      .padStart(2, "0")}`;
  } catch (e) {
    return dateString;
  }
};

// Convert API status to UI status
const mapOrderStatus = (status) => {
  const statusMap = {
    delivered: "delivered",
    shipped: "shipped",
    processing: "processing",
    pending_payment: "processing",
    cancelled: "cancelled",
    preparing: "processing",
    out_for_delivery: "shipped",
  };
  return statusMap[status] || "processing";
};

// Status display helper
const OrderStatus = ({ status }) => {
  const uiStatus = mapOrderStatus(status);

  switch (uiStatus) {
    case "delivered":
      return (
        <div className="flex items-center text-green-400">
          <CheckCircle className="w-4 h-4 ml-2" />
          <span>تحویل شده</span>
        </div>
      );
    case "shipped":
      return (
        <div className="flex items-center text-blue-400">
          <Truck className="w-4 h-4 ml-2" />
          <span>در حال ارسال</span>
        </div>
      );
    case "processing":
      return (
        <div className="flex items-center text-yellow-400">
          <Clock className="w-4 h-4 ml-2" />
          <span>در حال پردازش</span>
        </div>
      );
    case "cancelled":
      return (
        <div className="flex items-center text-red-400">
          <AlertTriangle className="w-4 h-4 ml-2" />
          <span>لغو شده</span>
        </div>
      );
    default:
      return (
        <div className="flex items-center text-gray-400">
          <Package className="w-4 h-4 ml-2" />
          <span>نامشخص</span>
        </div>
      );
  }
};

// Main component for user orders
export default function UserOrders() {
  const [orders, setOrders] = useState([]);
  const [searchQuery, setSearchQuery] = useState("");
  const [statusFilter, setStatusFilter] = useState("all");
  const [currentPage, setCurrentPage] = useState(1);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);
  const ordersPerPage = 3;

  const ORDER_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/orders/"
      : "https://allmaxsport.com/api/orders/";

  useEffect(() => {
    async function fetchOrders() {
      setIsLoading(true);
      setError(null);

      try {
        const userId = getCookie("userId");
        if (!userId) {
          throw new Error("کاربر وارد نشده است");
        }

        const response = await fetch(`${ORDER_API_URL}?user_id=${userId}`);

        if (!response.ok) {
          throw new Error("خطا در دریافت اطلاعات سفارش‌ها");
        }

        const data = await response.json();

        // Transform API data to match our component structure
        const transformedOrders = Array.isArray(data)
          ? data.map((order) => ({
              id: order.order_id,
              date: formatDate(order.order_date),
              estimatedDelivery: formatDate(order.estimated_delivery_date),
              items: order.items.map((item) => ({
                name: item.name,
                price: parseFloat(item.price) / item.quantity,
                quantity: item.quantity,
              })),
              status: order.order_status,
              totalPrice: parseFloat(order.total),
              carrierName: order.carrier,
              customerName: order.customer_name,
              shippingMethod: order.method,
              trackingCode: order.code,
            }))
          : [];

        setOrders(transformedOrders);
      } catch (err) {
        setError(err.message);
      } finally {
        setIsLoading(false);
      }
    }

    fetchOrders();
  }, []);

  // Filter orders based on search and status
  const filteredOrders = orders.filter((order) => {
    const matchesSearch =
      order.id.toLowerCase().includes(searchQuery.toLowerCase()) ||
      order.items.some((item) =>
        item.name.toLowerCase().includes(searchQuery.toLowerCase())
      );

    const matchesStatus =
      statusFilter === "all" || mapOrderStatus(order.status) === statusFilter;

    return matchesSearch && matchesStatus;
  });

  // Calculate pagination
  const indexOfLastOrder = currentPage * ordersPerPage;
  const indexOfFirstOrder = indexOfLastOrder - ordersPerPage;
  const currentOrders = filteredOrders.slice(
    indexOfFirstOrder,
    indexOfLastOrder
  );
  const totalPages = Math.ceil(filteredOrders.length / ordersPerPage);

  // Function to retry loading orders
  const handleRetry = () => {
    setCurrentPage(1);
    // The useEffect will run again after state change
    setOrders([]);
  };

  return (
    <div
      className="bg-zinc-900 p-6 rounded-lg shadow-xl border border-zinc-800"
      dir="rtl"
    >
      <h2 className="text-2xl font-bold mb-6 text-gray-100">سفارش‌های من</h2>

      {/* Search and Filter */}
      <div className="flex flex-col md:flex-row justify-between mb-6 gap-4">
        <div className="relative">
          <input
            type="text"
            placeholder="جستجو در سفارش‌ها..."
            className="pr-10 pl-4 py-2 bg-zinc-800 border border-zinc-700 rounded-lg w-full md:w-64 text-gray-200 placeholder-zinc-500 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
          <Search className="absolute top-2.5 right-3 h-5 w-5 text-gray-500" />
        </div>

        <div className="flex space-x-4 space-x-reverse">
          <div className="relative">
            <select
              className="py-2 px-4 pr-10 bg-zinc-800 border border-zinc-700 rounded-lg appearance-none text-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
            >
              <option value="all">همه وضعیت‌ها</option>
              <option value="delivered">تحویل شده</option>
              <option value="shipped">در حال ارسال</option>
              <option value="processing">در حال پردازش</option>
              <option value="cancelled">لغو شده</option>
            </select>
            <Filter className="absolute top-2.5 right-3 h-5 w-5 text-gray-500 pointer-events-none" />
          </div>
        </div>
      </div>

      {/* Loading State */}
      {isLoading && (
        <div className="text-center py-16 bg-zinc-800 bg-opacity-50 rounded-lg">
          <div className="animate-spin w-12 h-12 mx-auto mb-4">
            <RefreshCw className="w-12 h-12 text-blue-500" />
          </div>
          <h3 className="text-xl font-medium text-zinc-300">
            در حال بارگذاری سفارش‌ها...
          </h3>
        </div>
      )}

      {/* Error State */}
      {!isLoading && error && (
        <div className="text-center py-12 bg-zinc-800 bg-opacity-50 rounded-lg border border-red-900">
          <AlertTriangle className="w-16 h-16 mx-auto text-red-500 mb-4" />
          <h3 className="text-xl font-medium text-zinc-300 mb-2">
            خطا در بارگذاری سفارش‌ها
          </h3>
          <p className="text-zinc-500 mb-6">{error}</p>
          <button
            onClick={handleRetry}
            className="bg-blue-700 hover:bg-blue-600 text-white py-2 px-6 rounded-lg transition duration-300 flex items-center mx-auto"
          >
            <RefreshCw className="w-4 h-4 ml-2" />
            تلاش مجدد
          </button>
        </div>
      )}

      {/* Empty State */}
      {!isLoading && !error && orders.length === 0 && (
        <div className="text-center py-12 bg-zinc-800 bg-opacity-50 rounded-lg">
          <Package className="w-16 h-16 mx-auto text-zinc-600 mb-4" />
          <h3 className="text-xl font-medium text-zinc-300 mb-2">
            سفارشی یافت نشد
          </h3>
          <p className="text-zinc-500">شما هنوز هیچ سفارشی ثبت نکرده‌اید</p>
        </div>
      )}

      {/* No Results State */}
      {!isLoading &&
        !error &&
        orders.length > 0 &&
        filteredOrders.length === 0 && (
          <div className="text-center py-12 bg-zinc-800 bg-opacity-50 rounded-lg">
            <Search className="w-16 h-16 mx-auto text-zinc-600 mb-4" />
            <h3 className="text-xl font-medium text-zinc-300 mb-2">
              نتیجه‌ای یافت نشد
            </h3>
            <p className="text-zinc-500">
              هیچ سفارشی با معیارهای جستجوی شما پیدا نشد.
            </p>
          </div>
        )}

      {/* Orders List */}
      {!isLoading && !error && currentOrders.length > 0 && (
        <div className="space-y-6">
          {currentOrders.map((order) => (
            <div
              key={order.id}
              className="border border-zinc-800 rounded-lg p-4 bg-zinc-850 hover:border-blue-700 transition duration-300 bg-opacity-50 backdrop-blur-sm"
            >
              <div className="flex flex-col md:flex-row justify-between mb-4 pb-4 border-b border-zinc-800">
                <div className="flex flex-col mb-2 md:mb-0">
                  <span className="text-sm text-gray-500 mb-1">
                    شماره سفارش
                  </span>
                  <span className="font-semibold text-gray-300">
                    {order.id}
                  </span>
                </div>
                <div className="flex flex-col mb-2 md:mb-0">
                  <span className="text-sm text-gray-500 mb-1">تاریخ</span>
                  <span className="font-medium flex items-center text-gray-300">
                    <Calendar className="w-4 h-4 ml-1" />
                    {order.date}
                  </span>
                </div>
                <div className="flex flex-col mb-2 md:mb-0">
                  <span className="text-sm text-gray-500 mb-1">مبلغ کل</span>
                  <span className="font-bold text-blue-400">
                    {formatPrice(order.totalPrice)}
                  </span>
                </div>
                <div className="flex flex-col">
                  <span className="text-sm text-gray-500 mb-1">وضعیت</span>
                  <OrderStatus status={order.status} />
                </div>
              </div>

              {/* Shipment details for shipped orders */}
              {mapOrderStatus(order.status) === "shipped" && (
                <div className="mb-4 p-3 bg-blue-900 bg-opacity-20 rounded-lg border border-blue-800">
                  <div className="flex flex-wrap gap-4 justify-between text-sm">
                    <div>
                      <span className="text-gray-500">شرکت پستی:</span>
                      <span className="text-gray-300 mr-1">
                        {order.carrierName || "تیپاکس"}
                      </span>
                    </div>
                    {order.trackingCode && (
                      <div>
                        <span className="text-gray-500">کد رهگیری:</span>
                        <span className="font-mono text-blue-300 mr-1">
                          {order.trackingCode}
                        </span>
                      </div>
                    )}
                    {order.estimatedDelivery && (
                      <div>
                        <span className="text-gray-500">
                          تاریخ تحویل تقریبی:
                        </span>
                        <span className="text-gray-300 mr-1">
                          {order.estimatedDelivery}
                        </span>
                      </div>
                    )}
                  </div>
                </div>
              )}

              <div className="space-y-4">
                <h4 className="font-medium text-gray-300">اقلام سفارش</h4>
                {order.items.map((item, index) => (
                  <div
                    key={index}
                    className="flex flex-col md:flex-row justify-between items-start md:items-center py-2 border-b border-dashed border-zinc-800 last:border-0"
                  >
                    <div className="flex flex-col md:flex-row md:items-center mb-2 md:mb-0">
                      <div className="bg-zinc-800 rounded p-2 w-12 h-12 flex items-center justify-center text-gray-400 ml-3">
                        <Package />
                      </div>
                      <div>
                        <h5 className="font-medium text-gray-200">
                          {item.name}
                        </h5>
                        <span className="text-sm text-gray-500">
                          تعداد: {item.quantity}
                        </span>
                      </div>
                    </div>
                    <div className="font-semibold text-gray-300">
                      {formatPrice(item.price * item.quantity)}
                    </div>
                  </div>
                ))}
              </div>

              <div className="mt-4 flex justify-end">
                <button className="bg-blue-900 hover:bg-blue-800 text-blue-300 py-2 px-4 rounded-lg text-sm transition duration-300">
                  جزئیات بیشتر
                </button>
              </div>
            </div>
          ))}
        </div>
      )}

      {/* Pagination */}
      {!isLoading && !error && filteredOrders.length > ordersPerPage && (
        <div className="flex justify-center mt-8">
          <nav className="flex items-center space-x-2 space-x-reverse">
            <button
              onClick={() => setCurrentPage((prev) => Math.max(prev - 1, 1))}
              disabled={currentPage === 1}
              className={`p-2 rounded-md ${
                currentPage === 1
                  ? "text-gray-700 cursor-not-allowed"
                  : "text-gray-400 hover:bg-zinc-800"
              }`}
            >
              <ChevronRight className="w-5 h-5" />
            </button>

            {Array.from({ length: totalPages }, (_, i) => i + 1).map((page) => (
              <button
                key={page}
                onClick={() => setCurrentPage(page)}
                className={`px-3 py-1 rounded-md ${
                  currentPage === page
                    ? "bg-blue-700 text-white"
                    : "text-zinc-400 hover:bg-zinc-800"
                }`}
              >
                {page}
              </button>
            ))}

            <button
              onClick={() =>
                setCurrentPage((prev) => Math.min(prev + 1, totalPages))
              }
              disabled={currentPage === totalPages}
              className={`p-2 rounded-md ${
                currentPage === totalPages
                  ? "text-gray-700 cursor-not-allowed"
                  : "text-gray-400 hover:bg-zinc-800"
              }`}
            >
              <ChevronLeft className="w-5 h-5" />
            </button>
          </nav>
        </div>
      )}
    </div>
  );
}
